export default {
  name: "App",
  data: function data() {
    return {
      transitionName: "none"
    };
  },
  // vue监听路由对象$route的方法
  watch: {
    // watch $route 决定使用哪种过渡
    $route: function $route(to, from) {
      console.log(from); // '/'

      console.log(to); // '/next1'
      //to、from是最基本的路由对象，分别表示从(from)某个页面跳转到(to)另一个页面,to.path（表示要跳转到的路由地址），from.path同理。

      var routerDeep = ["/", "/next1"]; //找到to.path和from.path在routerDeep数组中的下标

      var toDepth = routerDeep.indexOf(to.path);
      var fromDepth = routerDeep.indexOf(from.path); //this.transitionName = toDepth <= fromDepth ? "fold-left" : "fold-right";

      if (toDepth == fromDepth) {
        this.transitionName = "none";
      } else if (toDepth < fromDepth) {
        this.transitionName = "fold-left";
      } else {
        this.transitionName = "fold-right";
      }
    }
  }
};